import Vue from 'vue'
import VueRouter from 'vue-router'
import NotFoundComponent from '@/components/NotFoundComponent'
import Root from '@/components/Root'
import Show from '@/components/Show'
import Test from '@/components/Test'
import d1 from '@/components/d1'
import d2 from '@/components/d2'
import d3 from '@/components/d3'
import d4 from '@/components/d4'

import d1d1 from '@/components/d1.1'

Vue.use(VueRouter)

const router = new VueRouter({
  mode: 'history',
  base: '/vue/',
  routes: [
    {
      path: '/',
      name: 'Root',
      component: Root,
      // component: () => import('@/components/Root'),
      meta: {
        title: '首页'
      }
    },
    {
      path: '/show',
      name: 'Show',
      component: Show,
      // component: () => import('@/components/Show'),
      meta: {
        title: '项目'
      }
    },
    {
      path: '/test',
      name: 'Test',
      component: d1d1,
      // component: () => import('@/components/Test'),
      meta: {
        title: '测试页面'
      }
    },
    {
      path: '/d1',
      name: 'd1',
      component: d1,
      // component: () => import('@/components/2-1'),
      meta: {
        title: 'DEMO'
      }
    },
    {
      path: '/d2',
      name: 'd2',
      component: d2,
      // component: () => import('@/components/2-1'),
      meta: {
        title: 'DEMO'
      }
    },
    {
      path: '/d3',
      name: 'd3',
      component: d3,
      // component: () => import('@/components/2-1'),
      meta: {
        title: 'DEMO'
      }
    },
    {
      path: '/d4',
      name: 'd4',
      component: d4,
      // component: () => import('@/components/2-1'),
      meta: {
        title: 'DEMO'
      }
    },
    {
      path: '*',
      component: NotFoundComponent,
      // component: () => import('@/components/NotFoundComponent'),
      meta: {
        title: '404 找不到页面'
      }
    }
  ]
})

router.beforeEach((to, from, next) => {
  if (to.meta.title) { // 如果设置标题，拦截后设置标题
    document.title = to.meta.title
  }
  next()
})

export default router



