<template>
  <div>
    <div id="d4" :style="{width: '720px', height: '510px'}"></div>
  </div>
</template>

<script>
// import echarts from "echarts";
var echarts = require("echarts/lib/echarts");

require("echarts/lib/chart/scatter");
require("echarts/lib/chart/effectScatter");
require("echarts/lib/component/calendar"); // 日历组件
require("echarts/lib/component/legendScroll"); // 图例组件

export default {
  name: "d4",
  data() {
    return {};
  },
  methods: {
    init: function() {
      function getVirtulData(year) {
        year = year || "2019";
        var date = +echarts.number.parseDate(year + "-01-01");
        var end = +echarts.number.parseDate(+year + 1 + "-01-01");
        var dayTime = 3600 * 24 * 1000;
        var data = [];
        for (var time = date; time < end; time += dayTime) {
          data.push([
            echarts.format.formatTime("yyyy-MM-dd", time),
            Math.floor(Math.random() * 10000)
          ]);
        }
        return data;
      }

      var data = getVirtulData(2018);

      var root2_1 = echarts.init(document.getElementById("d4"));
      // root2_1.setOption(option_root2_1);

      root2_1.setOption({
        // backgroundColor: '#404a59',

        title: {
          top: 30,
          text: "2018年每天的步数",
          subtext: "test",
          left: "center",
          textStyle: {
            color: "#fff"
          }
        },
        tooltip: {
          trigger: "item"
        },
        legend: {
          top: "30",
          left: "100",
          data: ["步数", "Top 12"],
          textStyle: {
            color: "#fff"
          }
        },
        calendar: [
          {
            top: 100,
            left: "center",
            range: ["2018-01-01", "2018-06-30"],
            splitLine: {
              show: true,
              lineStyle: {
                color: "#000",
                width: 4,
                type: "solid"
              }
            },
            yearLabel: {
              formatter: "{start}  1st",
              textStyle: {
                color: "#fff"
              }
            },
            itemStyle: {
              normal: {
                color: "#323c48",
                borderWidth: 1,
                borderColor: "#111"
              }
            }
          },
          {
            top: 340,
            left: "center",
            range: ["2018-07-01", "2018-12-31"],
            splitLine: {
              show: true,
              lineStyle: {
                color: "#000",
                width: 4,
                type: "solid"
              }
            },
            yearLabel: {
              formatter: "{start}  2nd",
              textStyle: {
                color: "#fff"
              }
            },
            itemStyle: {
              normal: {
                color: "#323c48",
                borderWidth: 1,
                borderColor: "#111"
              }
            }
          }
        ],
        series: [
          {
            name: "步数",
            type: "scatter",
            coordinateSystem: "calendar",
            data: data,
            symbolSize: function(val) {
              return val[1] / 500;
            },
            itemStyle: {
              normal: {
                color: "#ddb926"
              }
            }
          },
          {
            name: "步数",
            type: "scatter",
            coordinateSystem: "calendar",
            calendarIndex: 1,
            data: data,
            symbolSize: function(val) {
              return val[1] / 500;
            },
            itemStyle: {
              normal: {
                color: "#ddb926"
              }
            }
          },
          {
            name: "Top 12",
            type: "effectScatter",
            coordinateSystem: "calendar",
            calendarIndex: 1,
            data: data
              .sort(function(a, b) {
                return b[1] - a[1];
              })
              .slice(0, 12),
            symbolSize: function(val) {
              return val[1] / 500;
            },
            showEffectOn: "render",
            rippleEffect: {
              brushType: "stroke"
            },
            hoverAnimation: true,
            itemStyle: {
              normal: {
                color: "#f4e925",
                shadowBlur: 10,
                shadowColor: "#333"
              }
            },
            zlevel: 1
          },
          {
            name: "Top 12",
            type: "effectScatter",
            coordinateSystem: "calendar",
            data: data
              .sort(function(a, b) {
                return b[1] - a[1];
              })
              .slice(0, 12),
            symbolSize: function(val) {
              return val[1] / 500;
            },
            showEffectOn: "render",
            rippleEffect: {
              brushType: "stroke"
            },
            hoverAnimation: true,
            itemStyle: {
              normal: {
                color: "#f4e925",
                shadowBlur: 10,
                shadowColor: "#333"
              }
            },
            zlevel: 1
          }
        ]
      });
    }
  },
  mounted() {
    this.init();
  }
};
</script>

<style scoped>
#d4 {
  margin: 0 auto;
}
</style>



